.Login_form__btn__2PN5u button {
    cursor: pointer;
    display: inline-block;
    padding: 10px 30px;
    margin: 10px 20px;
    position: relative;
    overflow: hidden;
    border: 2px solid green;
    border-radius: 8px;
    color: green;
    transition: .2s ease-in-out;
}

.Login_form__btn__2PN5u button:before {
    content: "";
    background: linear-gradient(90deg, rgba(255,255,255,.1), rgba(255,255,255,.5));
    height: 50px;
    width: 50px;
    position: absolute;
    top: -8px;
    left: -75px;
    transform: skewX(-45deg);
}

.Login_form__btn__2PN5u button:hover:before {
    left: 150px;
    transition: .5s ease-in-out;
}

.Login_form__btn__2PN5u button:hover {
    background: green;
    color: #fff;
}

